.workout {
    border: 2px solid var(--primary-color);
    border-radius: 50px;
    padding: 10px;
    margin-left: 10px;
    margin-top: 10px;
    flex: 1;
}

.workout img {
    display: block;
    margin:auto;
    margin-top: 20px;
}

#change-buttons {
    margin-top: 10px;
    margin-left: 20px;
}

#change-buttons a {
    text-decoration: none;
    font-size: 1.5em;
    margin-right: 20px;
}